<template>
	<div class="projectList" v-loading='loading'>
		<div class="list" v-if="!createDate.type">
			<el-form :inline="true" :model="formInline" class="demo-form-inline" @keyup.enter.native="onSubmit">
				<el-form-item>
					<el-select v-model="formInline.role_id" placeholder="" style="width:140px;" clearable>
						<el-option v-for="item in role" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item>
					<el-select v-model="formInline.status" style="width:100px;" :value="formInline.status" clearable placeholder="">
						<el-option label="" value="1"></el-option>
						<el-option label="" value="2"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item>
					<el-input v-model="formInline.id" style="width:140px;" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-input v-model="formInline.account" style="width:140px;" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-input v-model="formInline.name" style="width:130px;" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-input v-model="formInline.phone" style="width:140px;" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="success" @click="onSubmit"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="setClick()"></el-button>
				</el-form-item>
			</el-form>
			<!---->
			<el-table :data="tableData" border style="width: 100%;">
				<el-table-column prop="id" label="" align="center">
				</el-table-column>
				<el-table-column prop="account" label="" align="center">
				</el-table-column>
				<el-table-column prop="name" label="" align="center">
				</el-table-column>
				<el-table-column prop="phone" label="" align="center">
				</el-table-column>
				<el-table-column prop="rname" label="" align="center">
				</el-table-column>
				<el-table-column label="" align="center">
					<template slot-scope="scope_">
						<switchEl :scope="scope_" :postUrl='switchPostUrl'></switchEl>
					</template>
				</el-table-column>
				<el-table-column label="" align="center">
					<template slot-scope="scope">
						<el-button @click="setClick(scope.row)" type="text"></el-button>
					</template>
				</el-table-column>
			</el-table>
		</div>
		<addUser v-if="createDate.type" v-on:back="back" :createDate="createDate"></addUser>
	</div>
</template>

<script>
	import addUser from "./addUser";
	import switchEl from "../public/dialog";
	export default {
		components: {
			addUser,
			switchEl
		},
		data() {
			return {
				loading:true,
				formInline: {
					role_id: '',
					status: '',
					id: '',
					account: '',
					name: '',
					phone: ''
				},
				tableData: [/*{id: 1,name: '',account:'123@qq.com',rname: "",
				//gender: '',
				phone: '18812110230',wechat: 'ha',status: '1'}*/],
				role: [],
				createDate: {
					type: false,
					data: '',
					name: '',
					state: '',
					role: []
				},
				switchPostUrl:'api/Role/changeAdminStatus'
			}
		},
		created: function() {
			/**/
			var self = this;
			self.loading=true;
			self.getPresonList({});
			/**/
			self.post('api/Role/roleManagement',{},
	  		function(data){
	  			if(data.data.code==200){
	  				self.role = data.data.data;
	  				self.createDate.role=data.data.data;
	  				self.loading=false;
	  			}else{
	  				self.$message.error(data.data.msg);
	  			}
	  		},function(error){
	  		});
		},
		methods: {
			onSubmit() {
				/**/
				this.getPresonList(this.formInline);
			},
			back() {
				this.getPresonList({});
				this.createDate.type = !this.createDate.type;
			},
			//
			setClick(row) {
				this.createDate.type = true;
				if(!row) {
					this.createDate.name = '';
					this.createDate.data = '';
					this.createDate.state = true;
				} else {
					this.createDate.name = '';
					this.createDate.data = row;
					this.createDate.state = false;
				}
			},
			/* */
			getPresonList(data){
				var self = this;
				self.post('api/Role/presonList', data,
				function(data) {
					if(data.data.code == 200) {
						self.tableData = data.data.data;
					} else {
						self.$message.error(data.data.msg);
					}
				},
				function(error) {
				});
			},
			get_wx(wx){
				if(wx==''||wx==null){
					return false;
				}else{
					return true;
				}
			}
		}
	}
</script>

<style>

</style>



