<template>
	<div class="create">
		<div class="templateTitle">
			
		</div>
		<el-form label-width="150px" class="demo-ruleForm w600">
			<el-form-item label="" required>
				<el-input v-model="fromMode.name" placeholder="" ></el-input>
			</el-form-item>
			<el-form-item label="" required>
				<el-input v-model="fromMode.phone" placeholder="" disabled></el-input>
			</el-form-item>
			<el-form-item label="">
				<el-select v-model="fromMode.gender" placeholder="" clearable>
					<el-option v-for="item in select_data.gender"
						:key="item"
						:label="item"
						:value="item">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="">
				<el-select v-model="card_type" @change="setCardType" placeholder="" clearable>
					<el-option v-for="item in select_data.card_type"
						:key="item"
						:label="item"
						:value="item">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="">
				<el-input v-model="card_num" placeholder="" @blur="setCardNum()" :disabled="card_type==''"></el-input>
			</el-form-item>
			<el-form-item label="">
				<el-date-picker
			      v-model="fromMode.birthday"
			      type="date"
			      placeholder="">
			    </el-date-picker>
			</el-form-item>
			<el-form-item label="">
				<el-select v-model="fromMode.constellation" placeholder="" clearable>
					<el-option v-for="item in select_data.constellation"
						:key="item"
						:label="item"
						:value="item">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="">
				<el-input v-model="fromMode.province" placeholder="" ></el-input>
		    	<!--<addressOption @areaChange='areaChange' :province='fromMode.province' :city='fromMode.city' :area='fromMode.area' type="1"></addressOption>-->
		    </el-form-item>
		    <el-form-item label="" >
		    	<el-input v-model="fromMode.city" placeholder="" ></el-input>
		    </el-form-item>
		    <el-form-item label="">
				<el-input v-model="fromMode.area" placeholder="" ></el-input>
			</el-form-item>
		    <el-form-item label="">
		    	<el-input v-model="fromMode.address" placeholder=""></el-input>
		    </el-form-item>
		    <el-form-item label="">
				<el-input v-model="fromMode.wechat" placeholder=""></el-input>
			</el-form-item>
			<el-form-item label="QQ">
				<el-input v-model="fromMode.qq" placeholder="QQ"></el-input>
			</el-form-item>
			<el-form-item label="">
				<el-input v-model="fromMode.email" placeholder=""></el-input>
			</el-form-item>
			<el-form-item label="">
				<el-select v-model="fromMode.blood_type" placeholder="" clearable>
					<el-option v-for="item in select_data.blood_type"
						:key="item"
						:label="item"
						:value="item">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="">
				<el-select v-model="fromMode.identity" placeholder="" clearable>
					<el-option v-for="item in select_data.identity"
						:key="item"
						:label="item"
						:value="item">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="">
				<el-select v-model="fromMode.education" placeholder="" clearable>
					<el-option v-for="item in select_data.education"
						:key="item"
						:label="item"
						:value="item">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="">
				<el-select v-model="fromMode.income" placeholder="" clearable>
					<el-option v-for="item in select_data.income"
						:key="item"
						:label="item"
						:value="item">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="">
				<el-select v-model="fromMode.marriage_love" placeholder="" clearable>
					<el-option v-for="item in select_data.marriage_love"
						:key="item"
						:label="item"
						:value="item">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="">
				<el-select v-model="fromMode.offspring" placeholder="" clearable>
					<el-option v-for="item in select_data.offspring"
						:key="item"
						:label="item"
						:value="item">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="">
				<el-select v-model="fromMode.commute_type" placeholder="" clearable>
					<el-option v-for="item in select_data.commute_type"
						:key="item"
						:label="item"
						:value="item">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="">
				<el-select v-model="fromMode.sport_preference" multiple placeholder="">
				    <el-option
				      v-for="item in select_data.sport_preference"
				      :key="item"
				      :label="item"
				      :value="item">
				    </el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="">
				<el-select v-model="fromMode.hobby" multiple placeholder="">
				    <el-option
				      v-for="item in select_data.hobby"
				      :key="item"
				      :label="item"
				      :value="item">
				    </el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="">
				<el-select v-model="fromMode.video_pay" multiple placeholder="">
				    <el-option
				      v-for="item in select_data.video_pay"
				      :key="item"
				      :label="item"
				      :value="item">
				    </el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="">
				<el-select v-model="fromMode.information" multiple placeholder="">
				    <el-option
				      v-for="item in select_data.information"
				      :key="item"
				      :label="item"
				      :value="item">
				    </el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="">
				<el-select v-model="fromMode.motion_location" placeholder="" clearable>
					<el-option v-for="item in select_data.motion_location"
						:key="item"
						:label="item"
						:value="item">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="">
				<el-select v-model="fromMode.motion_sum" placeholder="" clearable>
					<el-option v-for="item in select_data.motion_sum"
						:key="item"
						:label="item"
						:value="item">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="">
				<el-select v-model="fromMode.max_expenditure" placeholder="">
				    <el-option
				      v-for="item in select_data.max_expenditure"
				      :key="item"
				      :label="item"
				      :value="item">
				    </el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="">
				<el-select v-model="fromMode.year_price" placeholder="">
				    <el-option
				      v-for="item in select_data.year_price"
				      :key="item"
				      :label="item"
				      :value="item">
				    </el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="">
				<el-select v-model="fromMode.physical_condition" placeholder="">
				    <el-option
				      v-for="item in select_data.physical_condition"
				      :key="item"
				      :label="item"
				      :value="item">
				    </el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="">
				<el-select v-model="fromMode.like_sport_brand" multiple placeholder="">
				    <el-option
				      v-for="item in select_data.like_sport_brand"
				      :key="item"
				      :label="item"
				      :value="item">
				    </el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="">
				<el-select v-model="fromMode.match_sum" placeholder="" clearable>
					<el-option v-for="item in select_data.match_sum"
						:key="item"
						:label="item"
						:value="item">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="">
				<tabtag :data='fromMode.like_stars'></tabtag>
			</el-form-item>
			<el-form-item label="">
				<el-select v-model="fromMode.entertainment_type" placeholder="" clearable>
					<el-option v-for="item in select_data.entertainment_type"
						:key="item"
						:label="item"
						:value="item">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="">
				<el-select v-model="fromMode.game_type" placeholder="" clearable>
					<el-option v-for="item in select_data.game_type"
						:key="item"
						:label="item"
						:value="item">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="">
				<tabtag :data='fromMode.combat_team'></tabtag>
			</el-form-item>
			<el-form-item label="">
				<tabtag :data='fromMode.support_team'></tabtag>
			</el-form-item>
			<el-form-item>
				<el-button type="primary" @click="createClick()"></el-button>
				<el-button v-on:click="back"></el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	/*import addressOption from "../public/address"*/
	import tabtag from "../public/tabTag"
	export default {
		components: {
			tabtag,
			/*addressOption*/
		},
		data() {
			return {
				fromMode: {},
				card_num:'',
				card_type:'',
				rule:false,
				showPass: true,
				//
				select_data:{
					gender:[],//
					blood_type:[],//
					card_type:[],//
					identity:[],//
					income:[],//
					constellation:[],//
					education:[],//
					marriage_love:[],//
					offspring:[],//
					commute_type:[],//
					sport_preference:[],//
					hobby:[],//
					video_pay:[],//
					information:[],//
					motion_location:[],//
					max_expenditure:[],//
					year_price:[],//
					like_sport_brand:[],//
					entertainment_type:[],//
					game_type:[],//
					match_sum:[],//
					motion_sum:[],//
					physical_condition:[]//
				}
			}
		},
		props: ['createDate'],
		created: function() {
			this.getSelection();
			this.fromMode = this.createDate.data
			this.card_type = '';
			var card_item = this.fromMode.card_list;
			for(var z=0;z<card_item.length;z++){
				if(card_item[z].card_type==''){
					this.card_num = card_item[z].card_num
				}else{
					this.card_num = '';
				}
			}
		},
		methods: {
			/*//
			areaChange(data){
				this.fromMode.province=data.province
				this.fromMode.city=data.city
				this.fromMode.area=data.area
			},*/
			back() {
				this.$emit("back");
			},
			createClick() {
				var self = this;
				var card_item = self.fromMode.card_list;
				switch(''){
		  			case self.fromMode.account:
			  			self.$message.error('');
			  			return
		  			case self.fromMode.name:
			  			self.$message.error('');
			  			return
		  		}
				for(var i=0;i<card_item.length;i++){
					if(card_item[i].card_num==''){
						card_item.splice(i,1);
					}
				}
		  		self.post('api/user/editUserInfo',self.fromMode,
			  	function(data){
			  		if(data.data.code==200){
			  			self.$message.success(data.data.msg);
		  				self.back();
			  		}else{
			  			self.$message.error(data.data.msg);
			  		}
			  	},function(error){
			  	});
			},
			getSelection(){
				var self = this;
				self.post('api/user/getSelection',{},
			  	function(data){
			  		if(data.data.code==200){
			  			self.select_data = data.data.data;
			  		}else{
			  			self.$message.error(data.data.msg);
			  		}
			  	},function(error){
			  	});
			},
			setCardType(value){
				if(value!=''){
					this.card_type = value;
					var card_item = this.fromMode.card_list;
					for(var z=0;z<card_item.length;z++){
						if(card_item[z].card_type==this.card_type){
							this.card_num = card_item[z].card_num
							return;
						}
					}
					this.card_num='';
					card_item.push({card_type:this.card_type,card_num:this.card_num});
				}
			},
			setCardNum(){
				var card_item = this.fromMode.card_list;
				for(var z=0;z<card_item.length;z++){
					if(card_item[z].card_type == this.card_type){
						card_item[z].card_num = this.card_num;
					}
				}
			}
		}
	}
</script>

<style scoped>
	.el-select{width:100%;}
</style>


