<template>
	<div class="projectList" v-loading='loading'>
		<div class="list" v-if="!createDate.type">
			<el-form :inline="true" :model="formInline" class="demo-form-inline" @keyup.enter.native="onSubmit">
				<el-form-item>
					<el-select v-model="formInline.status" style="width:100px;" :value="formInline.status" clearable placeholder="">
						<el-option label="" value="1"></el-option>
						<el-option label="" value="2"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item>
					<el-input v-model="formInline.id" style="width:140px;" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-input v-model="formInline.name" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="success" @click="onSubmit"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="setClick()"></el-button>
				</el-form-item>
			</el-form>
			<!---->
			<el-table :data="tableData" border style="width: 100%;">
				<el-table-column prop="id" width="150" label="" align="center">
				</el-table-column>
				<el-table-column prop="name" width="150" label="" align="center">
				</el-table-column>
				<el-table-column prop="remarks" label="" align="center">
				</el-table-column>
				<el-table-column label="" width="80" align="center">
					<template slot-scope="scope">
						<switchEl :scope="scope" :postUrl='switchPostUrl'></switchEl>
				    </template>
				</el-table-column>
				<el-table-column label="" width="100" align="center">
					<template slot-scope="scope">
				        <el-button @click="setClick(scope.row)" type="text"></el-button>
				    </template>
				</el-table-column>
			</el-table>
		</div>
		<create v-if="createDate.type" v-on:back="back" :createDate="createDate"></create>
	</div>
</template>

<script>
	import create from "./createRole";
	import switchEl from "../public/dialog";
	export default {
		components: {
			create,
			switchEl
		},
		data() {
			return {
				loading:true,
				formInline: {
					status: '',
					id: '',
					name: ''
				},
				tableData: [/*{id:1,name:"",desc: '',status: 1}*/],
		        createDate:{
		           type:false,
		           data:'',
		           name:'',
		           state:''
		        },
		        switchPostUrl:'api/Role/changeRoleStatus'
			}
		},
		created: function() {
			this.onSubmit();
		},
		methods: {
			onSubmit() {
				/**/
				var self = this;
				self.loading=true;
				self.post('api/Role/roleManagement',self.formInline,
		  		function(data){
		  			if(data.data.code==200){
		  				self.tableData = data.data.data;
		  				self.loading=false;
		  			}else{
		  				self.$message.error(data.data.msg);
		  			}
		  		},function(error){
		  		});
			},
			//
			setClick(row){
				this.createDate.type=true;
				if(!row){
					this.createDate.name='';
					this.createDate.data='';
				}else{
					this.createDate.name='';
					this.createDate.data=row;
				}
			},
		    back(){
		    	this.onSubmit();
		    	this.createDate.type = !this.createDate.type;
		    }
		}
	}
</script>

<style>
</style>


