import Vue from 'vue'
import Router from 'vue-router'
import login from '@/components/login'
import home from '@/components/home'
import contents from '@/components/contents'
/**/
import projectList from '@/components/project/projectList'
import create from '@/components/project/create'
import productList from '@/components/project/product'
import userImport from '@/components/project/userImport'
/**/
import userData from '@/components/user/userData'
import common from '@/components/user/common'
import work from '@/components/user/work'
import repeat from '@/components/user/repeat'
/**/
import pandect from '@/components/table/pandect'
import product from '@/components/table/product'
import behavior from '@/components/table/behavior'
import portrait from '@/components/table/portrait'
import analysis from '@/components/table/analysis'
/**/
import sms from '@/components/sms/sms'
import send from '@/components/sms/send'
/**/
import dynamic from '@/components/system/dynamic'
import userNumber from '@/components/system/userNumber'
import role from '@/components/system/role'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: login
    },
    {
      path: '/login',
      component: login
    },
    {
    	path: '/home',
    	name:'home',
    	component: home
    },
    {
      path: '/contents',
      name:'/contents',
      component: contents,
      children: [
      	{
          path: '/project/product',// 
          name: '/project/product',
          component: productList
        },
        {
          path: '/project/userImport',// 
          name: '/project/userImport',
          component: userImport
        },
        {
        	path: '/project/projectList',// 
          name: '/project/projectList',
          component: projectList
        },
        {
          path: '/project/create',//
          name: '/project/create',
          component: create
        },
        {
          path: '/user/userData',// 
          name: '/user/userData',
          component: userData
        },
        {
          path: '/user/common',//
          name: '/user/common',
          component: common
        },
        {
          path: '/user/work',//
          name: '/user/work',
          component: work
        },
        {
          path: '/user/repeat',//
          name: '/user/repeat',
          component: repeat
        },
        {
          path: '/table/pandect',//
          name: '/table/pandect',
          component: pandect
        },
        {
          path: '/table/product',//
          name: '/table/product',
          component: product
        },
        {
          path: '/table/behavior',//
          name: '/table/behavior',
          component: behavior
        },
        {
          path: '/table/portrait',//
          name: '/table/portrait',
          component: portrait
        },
        {
          path: '/table/analysis',//
          name: '/table/analysis',
          component: analysis
        },
        {
          path: '/sms/sms',//
          name: '/sms/sms',
          component: sms
        },
        {
          path: '/sms/send',//
          name: '/sms/send',
          component: send
        },
        {
          path: '/system/dynamic',//  
          name: '/system/dynamic',
          component: dynamic
        },
        {
          path: '/system/userNumber',//
          name: '/system/userNumber',
          component: userNumber
        },
        {
          path: '/system/role',//
          name: '/system/role',
          component: role
        }
      ]
    }
  ]
})



